$color-primary:  #E1F8D9;
$color-secondary:#25588B;


body {
    font-family: "Libre Franklin", sans-serif;
    margin: 0;
    color: $color-secondary;
}
.menu {
    background-color: white;
    font-size: 0.5rem;
    display: flex;
    justify-content: space-between;
}
    .menu__list {
        margin: 0;
        padding: 1px;
        display: flex;
        justify-content: right;
        list-style: none;
    }
.menu__item {
    width: 100%;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  label {
    margin-bottom: 10px;
    align-items: right;
  }
  
  input, textarea {
    margin-bottom: 20px;
    padding: 10px;
    border: none;
    width: 60%;
    background-color:$color-primary ;
  }
  
  input[type="submit"] {
    background-color: $color-primary;
    color: $color-secondary;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    width: 20%;

  }

  input[type="file"] {
    align-items: left;
    width: 20%;

  }
  
  input[type="submit"]:hover {
    background-color: $color-primary;
  
  }
  
.logo{
    margin: 0;
    padding: 1px;
    display: flex;
    justify-content: left;
    list-style: none;
    font-size: 1rem;
    place-content: left;
    text-align: left;
}

img {
    width: auto;
}

.menu__link {
        display: block;
        padding: 5px 5px;
        text-decoration: none;
        color: $color-secondary;
    }

    .menu__link:hover, .menu__link:focus {
        background-color: $color-primary;
        color:$color-secondary;
    }
    .header {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        max-height: fit-content;
    }
    .header__image--second {
        grid-column: 1;
        grid-row: 1;
        width: 100%;
        z-index: 1;
        object-fit: cover;
        max-height: fit-content;

    }
    .header__image--first {
        grid-column: 1;
        grid-row: 1;
        width: 100%;
        height: 100%;
        z-index: 2;
    }
    .kontejner__grid {
        display: grid;
        grid-template-columns: 1;
        grid-template-rows: 2;
        background-color: $color-primary;
        width: 70%;
        border-radius: 0px 50px 50px 0px ; 
        margin-top: 5%;
        padding-left: 3%;
        padding: 2%;
         }

         .kontejner__item {
            grid-column: 1;
            grid-row: 1;
            font-size: 2em;
            font-family: "Libre Franklin", sans-serif;
        }

        .kontejner__text {
            display: grid;
            grid-template-columns: r(3, 1fr);
            grid-template-rows: r(5, 1fr);
            font-size: 1rem;
            font-family: "Libre Franklin", sans-serif;
            gap:0;
            width: 50%;
            grid-column: 1;
            grid-row: 2;
}

.prodej__grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    padding: 2%;
    gap: 1%;
}

.prodej__item {
    grid-row: 1;
    grid-column: 1;
    object-fit: contain;
}

.kontejner__title {
font-size: 1em;
font-family: "Libre Franklin", sans-serif;
font-weight: 200;
letter-spacing: 8%;
}

.item {
width: fit-content;
font-weight: 150;
}
